import { getVirtualHostOptions } from '../common/component-handler/press-component';
import { addUnit } from '../common/utils/add-unit';
import { style } from '../common/utils/style';
import PressAvatar from '../press-avatar/press-avatar.vue';

import props from './computed';


export default {
  name: 'PressAvatarGroup',
  options: {
    ...getVirtualHostOptions(true, false),
    styleIsolation: 'shared',
  },
  components: {
    PressAvatar,
  },
  mixins: [props],
  emits: [
    'showMore',
  ],
  data() {
    return {

    };
  },
  computed: {
    showUrl() {
      return this.urls.slice(0, this.maxCount);
    },
    extraText() {
      const { extraValue, urls, showUrl } = this;
      return extraValue || urls.length - showUrl.length;
    },
    wrapStyle() {
      return style(this.customStyle);
    },
    textStyle() {
      return style({
        color: '#fff',
        fontSize: this.size * 0.4,
        textAlign: 'center',
      });
    },
  },
  methods: {
    addUnit,
    getAvatarUrl(item) {
      if (typeof item === 'object') {
        return item[this.keyName || 'url'];
      }
      return item;
    },
    clickHandler() {
      this.$emit('showMore');
    },
    getItemStyle(index) {
      const { size, gap } = this;
      const res =  style({
        marginLeft: index === 0 ? 0 : addUnit(-size * gap),
        width: addUnit(size),
        height: addUnit(size),
      });
      return res;
    },
  },
};
